.button {
    border-radius: 50%;
    background: #5acced;
    -webkit-box-shadow: 20px 20px 60px #3b879c, -20px -20px 60px #79ffff;
            box-shadow: 20px 20px 60px #3b879c, -20px -20px 60px #79ffff;
    width: 14rem;
    height: 14rem;
    cursor: pointer;
    -webkit-animation: buttonAnimation 4s linear infinite;
            animation: buttonAnimation 4s linear infinite;
  }
  
  @keyframes buttonAnimation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(359deg);
    }
  }